.project {
	-ms-flex: 1 1;
	    flex: 1 1;
	margin: 1em;
	position: relative;
	-webkit-transition: -webkit-transform 0.2s ease;
	transition: -webkit-transform 0.2s ease;
	-o-transition: transform 0.2s ease;
	transition: transform 0.2s ease;
	transition: transform 0.2s ease, -webkit-transform 0.2s ease;
}

.project:hover {
	-webkit-transform: scale(1.02);
	    -ms-transform: scale(1.02);
	        transform: scale(1.02);
}

.project--coming-soon:after {
	content: 'Coming Soon';
	display: block;
	position: absolute;
	top: 1.25em;
	left: -2.5em;
	background-color: white;
	color:black;
	-webkit-transform: rotate(-30deg);
	    -ms-transform: rotate(-30deg);
	        transform: rotate(-30deg);
	z-index: 15;
	font-weight: 300;
	padding: 0.25em 3em;
	font-size: 0.7em;
	opacity: 0.7;
}


.project__card {
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	background-color: white;
	position: relative;
	display: block;

	width: 100%;
  	padding-bottom: 56.25%;
  	z-index: 10;

  	color: rgb(180,180,180);

  	
/*
  	border-width: 1px;
  	border-style: solid; 
  	border-color: rgba(0,0,0,0.1);*/
  	
}

.project__card:focus {
	outline: none;
}


.project__description {
	padding: 0.75em;
	position: absolute;
	bottom: 0;
	right: 0;
	opacity: 0.7;
	font-weight: 300;
	font-size: 0.8rem;
	display: none;
}

@media only screen and (min-width: 600px) {
  .project__description {
    display: block;
  }
}


.project__category-icon {
	display: inline-block;
	margin-right: 0.5em;
}

.project:hover:before {
	-webkit-box-shadow: rgba(14, 21, 47, 0.5) 0px 20px 30px;
	        box-shadow: rgba(14, 21, 47, 0.5) 0px 20px 30px;
}

.project:before {
	content: '';
	display: block;
	-webkit-transition: -webkit-box-shadow 0.2s ease;
	transition: -webkit-box-shadow 0.2s ease;
	-o-transition: box-shadow 0.2s ease;
	transition: box-shadow 0.2s ease;
	transition: box-shadow 0.2s ease, -webkit-box-shadow 0.2s ease;
    -webkit-box-shadow: rgba(14, 21, 47, 0.3) 0px 20px 30px;
            box-shadow: rgba(14, 21, 47, 0.3) 0px 20px 30px;
    width: 90%;
    height: 90%;
    position: absolute;
    top: 5%;
    left: 5%;
    z-index: 5;
}


